:root {
	--main-bg-color: #222222;
	--main-div-color: #131313;
	--cream-color: #f9efd8;
	--light-purple: #b5aee0;
	--deep-champagne: #f2d4ac;
	--little-girl-pink: #f7c4d4;
	--vodka-color: #b8c4f1;
	--text-white: #fff;
	--text-black: #000;
	--text-gray: #777;
	--link-hover-color: #d6a3e4;
	--font-stack: "Lexend", "Poppins", "Roboto", "Syne", sans-serif;
	--fetch-dark-purple: #310c38;
	--fetch-medium-purple: #890075;
	--width: 95%;
	--max-width: 1800px;
	--border-radius: 20px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Lexend";
	color: var(--text-white);
	font-weight: 400;
	-webkit-font-smoothing: antialiased;
	text-decoration: none;
	list-style: none;
	line-height: 110%;
}

html,
body {
	width: 100%;
	height: 100%;
	background-color: var(--main-bg-color);
}

body {
	min-height: 100vh;
}

h1 {
	font-size: clamp(2rem, 7vw, 6rem);
	text-align: left;
	/* can change from 40px to 6rem or lower, leave for now , can also change from 4-7 vw*/
}

h3 {
	font-size: clamp(0.25rem, 2vw, 1.5rem);
	padding-bottom: 1rem;
	color: #b5aee0;
	text-align: center;
}

h6 {
	padding: 1rem;
}

main {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2rem 0;
}

#hero {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 5rem;
}

.container {
	position: relative;
	width: var(--width);
	max-width: var(--max-width);
	background-color: var(--main-div-color);
	padding: 2rem;
	border-top-left-radius: var(--border-radius);
	border-top-right-radius: var(--border-radius);
	border-bottom-left-radius: var(--border-radius);
}

.nav__container {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	margin: 1rem 0 2rem 0;
}

nav {
	width: 450px;
	position: relative;
	font-size: 2rem;
}

nav ul {
	display: flex;
	justify-content: space-between;
}

nav ul li a {
	opacity: 0.8;
	opacity: 0.8;
	text-decoration: overline;
	font-size: clamp(0.5rem, 2vw, 2rem);
}

a:hover {
	color: #890075;
	cursor: pointer;
}

.result_card {
	background-color: black;
}

.hero__text {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	margin-bottom: 1rem;
}

.sub__header {
	grid-column: span 12;
	display: flex;
	align-items: center;
	margin-bottom: 1rem;
}

.circle {
	width: 0.5rem;
	height: 0.5rem;
	background-color: #d6a3e4;
	margin-right: 0.5rem;
	border-radius: 50%;
	margin-bottom: 0.125rem;
}

.sub__header h4 {
	font-weight: bold;
}

.hero__header {
	grid-column: span 12;
	margin-bottom: 1rem;
}

.hero__intro {
	grid-column: span 8;
	margin-bottom: 1rem;
	font-family: "Lexend", sans-serif;
	opacity: 0.8;
	font-size: 2vmax;
	padding-top: 0.25rem;
	justify-content: flex-end;
}


/* main image container */

.image__container {
	position: relative;
	max-width: 100%;
	padding-top: 3rem;
}

.main__hero__image {
	position: absolute;
	top: 0;
	left: 0;
}

.bottom__div {
	position: relative;
	width: var(--width);
	padding-top: 30px;
	max-width: var(--max-width);
	display: flex;
}

.left {
	position: absolute;
	width: 50%;
	height: 100%;
	top: 0;
	left: 0;
}

.curve {
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: var(--main-div-color);
}

.cover {
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: var(--main-bg-color);
	border-top-right-radius: var(--border-radius);
	z-index: 1;
}

.right {
	position: absolute;
	top: 0;
	padding: 1rem;
	left: 50%;
	width: 50%;
	min-height: 20px;
	background-color: var(--main-div-color);
	border-bottom-left-radius: var(--border-radius);
	border-bottom-right-radius: var(--border-radius);
}

.services {
	padding: 1rem;
	width: 100%;
	text-align: center;
	background-color: var(--main-bg-color);
	border-radius: calc(var(--border-radius) - 1rem);
}

.services {
	padding: 1rem;
	width: 100%;
	text-align: center;
	background-color: var(--main-bg-color);
	border-radius: calc(var(--border-radius) - 1rem);
}

/* Cards */

.sortingButton {
	padding: 0.5rem;
	margin-top: 2rem;
	border-radius: 5px;
	justify-content: flex-start;
	align-items: center;
	display: flex;
	text-align: center;
	color: var(--light-purple);
	background: #222222;
	border-color: transparent;
}

section {
	padding-left: 1rem;
	padding-right: 1rem;
	padding-top: 2rem;
	padding-bottom: 3rem;
	text-align: center;
	display: block;
}

section .flex {
	display: inline-flex;
	justify-content: center;
	align-items: center;
}

section .posts {
	margin: 2rem;
	grid-template-columns: repeat(12, 1fr);
	grid-template-rows: 1;
	gap: 1rem;
	justify-content: center;
}

section .blog-card {
	flex: 1;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-content: space-between;
	width: 100%;
	border-radius: 1rem;
	background-color: #222222;
	padding: 1.25rem;
	// box-shadow: 1px 3px 10px 0px rgba(255, 255, 255, 0.63);
	// -webkit-box-shadow: 1px 3px 10px 0px rgba(255, 255, 255, 0.63);
	// -moz-box-shadow: 1px 3px 10px 0px rgba(255, 255, 255, 0.63);
}

.matchCard {
	display: flex;
	margin-top: 1rem;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-content: space-between;
	font-family: "Lexend";
	font-style: var(--light-purple);
	width: 100%;
	border-radius: 1rem;
	color: #131313;
	background-color: #222222;
	padding: 1.25rem;
	/* Can use shadows down the road if needed, looks bad with dark background */
	// box-shadow: 2px 7px 14px 0px rgba(255, 255, 255, 0.63);
	// -webkit-box-shadow: 2px 7px 14px 0px rgba(255, 255, 255, 0.63);
	// -moz-box-shadow: 2px 7px 14px 0px rgba(255, 255, 255, 0.63);
}

.matchImage {
	margin-bottom: 0.75rem;
	height: auto;
	object-position: bottom;
	width: 100%;
	display: flex;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 2rem;
}

section .blog-card img {
	margin-bottom: 0.75rem;
	aspect-ratio: 1 / 1;
	max-height: 100%;
	object-fit: cover;
	object-position: bottom;
	width: auto;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 2rem;
}
section .blog-card p {
	margin-bottom: 0;
	font-size: 1rem;
	font-weight: bold;
	line-height: 1.5;
	margin-bottom: 1rem;
	color: white;
}

/* Button styles */

section .btn {
	display: inline-block;
	padding: 0.5rem 1rem;
	text-align: center;
	text-decoration: none;
	font-size: 1rem;
	font-weight: bold;
	color: black;
	background-color: var(--light-purple);
	border-radius: 0.5rem;
}

section .btn:hover {
	background-color: var(--fetch-dark-purple);
	color: white;
	cursor: pointer;
}

section .btnSort {
	display: flex;
	margin-bottom: 2rem;
	grid-row: inherit;
	width: auto;
	align-items: center;
	justify-content: space-around;
	padding: 0.5rem 1rem;
	text-align: center;
	font-family: roboto;
	text-decoration: none;
	font-size: 1.2rem;
	color: white;
	background-color: #222222;
	border-radius: 0.5rem;
}

.nextBtn {
	display: flex;
	grid-row: inherit;
	align-items: right;
	padding: 0.25rem 1rem;
	object-fit: cover;
	font-size: 1.2rem;
	border-color: transparent;
	background-color: var(--cream-color);
}

.prevBtn {
	display: flex;
	grid-row: inherit;
	align-items: left;
	padding: 0.25rem 1rem;
	object-fit: cover;
	font-size: 1.2rem;
	border-color: transparent;
	background-color: var(--cream-color);
}

.resetBtnDiv {
	background-color: #131313;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.resetBtn {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-bottom: 2rem;
	grid-row: inherit;
	width: auto;
	padding: 0.5rem 1rem;
	text-align: center;
	font-family: "Lexend";
	text-decoration: none;
	font-size: 1.2rem;
	color: white;
	background-color: #222222;
	border-radius: 0.5rem;
}

.resetBtn p {
	color: var(--light-purple);
}

.matchBtnDiv {
	background-color: #131313;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.matchBtn {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-bottom: 2rem;
	grid-row: inherit;
	width: auto;
	align-items: center;
	justify-content: space-around;
	padding: 0.5rem 1rem;
	text-align: center;
	font-family: roboto;
	text-decoration: none;
	font-size: 1.2rem;
	color: white;
	background-color: #222222;
	border-radius: 0.5rem;
}

.matchBtn p {
	color: var(--light-purple);
}

/* Carousel */

.carousel {
	position: relative;
	width: 100%;
	margin-top: 1rem;
	overflow: hidden;
}

.carousel__inner {
	position: relative;
	display: flex;
	width: 200%;
	animation: marquee 15s linear infinite;
}

.item {
	flex: 1;
	justify-content: center;
	align-items: center;
	display: flex;
	justify-content: center;
	align-items: center;
}

.item img {
	width: 60%;
}

.left__carousel {
	position: absolute;
	left: 0;
	top: 0;
	width: 5%;
	height: 100%;
	background: linear-gradient(270deg, transparent, var(--main-div-color));
	z-index: 1;
}

.right__carousel {
	position: absolute;
	right: 0;
	top: 0;
	width: 5%;
	height: 100%;
	background: linear-gradient(90deg, transparent, var(--main-div-color));
	z-index: 1;
}

@keyframes marquee {
	0% {
		transform: translate(0);
	}

	100% {
		transform: translateX(-50%);
	}
}

#about {
	position: relative;
	width: 100%;
	display: flex;
	justify-content: center;
}

.about__container {
	position: relative;
	top: -5rem;
	width: var(--width);
	max-width: var(--max-width);
	display: grid;
	grid-template-columns: repeat(12, 1fr);
}

.about__text {
	grid-column: span 5;
	padding-left: 2rem;
}

@media screen and (max-width: 600px) {
	section .posts {
		margin: 0;
		padding-bottom: 1rem;
	}

	nav {
		position: relative;
		font-size: 12px;
	}

	.hero__intro {
		grid-column: span 12;
	}
}

@media screen and (min-width: 600px) and (max-width: 767px) {
	section .posts {
		margin: 0;
		padding-bottom: 1rem;
	}

	nav {
		position: relative;
		font-size: 12px;
	}

	.hero__intro {
		grid-column: span 12;
	}
}

@media screen and (min-width: 768px) and (max-width: 991px) {
	section .posts {
		margin: 0;
		padding: 1rem;
	}

	nav {
		position: relative;
		font-size: 12px;
	}
}

@media screen and (min-width: 992px) and (max-width: 1199px) {}

